}

.Header-Tabs{
  display:flex;
  justify-content: space-around;
}

.Header-logo{
  width: 13vw;
}

.Header-Buttons{
  padding: 20px 40px;
  cursor: pointer;
}

.Header-navigation-phone{
  display: none;
  padding: 10px;
  position: fixed;
  right: 0px;
  z-index: 5;

}

@media only screen and (max-width: 960px) and (min-width: 600px) {
